import React, { PureComponent } from "react";
import { Row, Col} from 'react-bootstrap';
import "./CourseDescription.css";

class CourseDescriptionQuery extends PureComponent {

	handleClickChange = event => {
		this.props.handleClickChange(event, this.props.id);
	};

	render() {
		console.log(this.props.color_text)
		return (
			<div>
				<div className="courseContainerQuery">
					<Row>
						<Col md={12}>
							<Row>
								<Col md={12}>
									<h4>
										{this.props.title}
									</h4>
								</Col>
							</Row>
							<Row>
								<Col md={2}>
									<img src={this.props.icon+".png"} className="imgSmall"/>
								</Col>
								<Col md={2}>
									{this.props.level}
								</Col>
								<Col md={4}>
									<mark style={{"background-color":this.props.color_text}}>{this.props.taxon}</mark>
								</Col>
								<Col md={4}>
									<button 
										type="button"
										class="btn btn-primary"
										onClick={this.handleClickChange}
										>
										Ver
									</button>
								</Col>
							</Row>
						</Col>
					</Row>
				</div>
				<div className="courseSpace" />
			</div>
		);
	}
}

export default CourseDescriptionQuery;