import React, { PureComponent } from "react";
import { Grid, Row, Col } from 'react-bootstrap';
import CourseDescription from "./CourseDescription";
import CourseDescriptionSummary from "./CourseDescriptionSummary";
import CourseDescriptionQuery from "./CourseDescriptionQuery";
import "./App.css";
import peopleListJson from "./people.json";
import survey from "./survey_processed.json";
import query from "./query.json";

//https://thenounproject.com/search/?q=music&i=2624927
//http://educativo.insor.gov.co/diccionario/diccionario-cotidiano/

const color_code = {
  "1":"#9A6637",
  "2":"#FFBE7F",
  "3":"#9B98FF",
  "4":"#FB0F13",
  "5":"#0725F9",
  "6":"#2BFB33",
  "7":"#CC40F7",
  "8":"#FDCACE",
  "9":"#147C0E",
}
console.log(query)
let obj = survey.filter(obj => obj.data_experience == "1-3")

class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      typedValue:"",
      query_courses:[],
      main_content:query[0]
    };
  }

  handleSearchChange = event => {
    let typedValue;
    typedValue = event.target.value
    let query_result = query.filter(obj => obj.query_str.toLowerCase().includes(typedValue.toLowerCase()))
    console.log(query_result)
    this.setState({
      typedValue: typedValue,
      query_courses:query_result
    });
  };

  handleClickChange = (event, id) => {
    console.log(id)
    console.log(":((((")
    this.setState({
      main_content:query.filter(obj => obj.id==id)[0]
    });
  };

  handleClickChangeSug = (event, id) => {
    console.log(id)
    console.log(":(((( Sug")
    this.setState({
      main_content:query.filter(obj => obj.id==id)[0]
    });
  };

  render() {

    let query_courses = this.state.query_courses
    let query_size = query_courses.length
    let max_size_query = 3
    if (query_size < max_size_query){
      max_size_query=query_size
    }
    query_courses = query_courses.slice(0, max_size_query)
    let main_content = this.state.main_content
    console.log("main_content - - - - - - - - ")
    console.log(main_content)

    let sug1;
    let sug2;
    let sug3;

    if (main_content.suggested.length == 0){
      sug1= <div> - </div>
      sug2= <div> - </div>
      sug3= <div> - </div>
    } else if (main_content.suggested.length == 1){
      sug1= <CourseDescriptionSummary
                  id={main_content.suggested[0].id}
                  title={main_content.suggested[0].title}
                  taxon={main_content.suggested[0].text_taxon}
                  color_text={color_code[main_content.suggested[0].btaxon]}
                  icon={main_content.suggested[0].type}
                  level={main_content.suggested[0].level}
                  brief={main_content.suggested[0].brief}
                  handleClickChange={this.handleClickChangeSug}
                />
      sug2= <div> - </div>
      sug3= <div> - </div>
    } else if (main_content.suggested.length == 2){
      sug1= <CourseDescriptionSummary
                  id={main_content.suggested[0].id}
                  title={main_content.suggested[0].title}
                  taxon={main_content.suggested[0].text_taxon}
                  color_text={color_code[main_content.suggested[0].btaxon]}
                  icon={main_content.suggested[0].type}
                  level={main_content.suggested[0].level}
                  brief={main_content.suggested[0].brief}
                  handleClickChange={this.handleClickChangeSug}
                />
      sug2= <CourseDescriptionSummary
                  id={main_content.suggested[1].id}
                  title={main_content.suggested[1].title}
                  taxon={main_content.suggested[1].text_taxon}
                  color_text={color_code[main_content.suggested[1].btaxon]}
                  icon={main_content.suggested[1].type}
                  level={main_content.suggested[1].level}
                  brief={main_content.suggested[1].brief}
                  handleClickChange={this.handleClickChangeSug}
                />
      sug3= <div> - </div>
    } else if (main_content.suggested.length == 3){
      sug1= <CourseDescriptionSummary
                  id={main_content.suggested[0].id}
                  title={main_content.suggested[0].title}
                  taxon={main_content.suggested[0].text_taxon}
                  color_text={color_code[main_content.suggested[0].btaxon]}
                  icon={main_content.suggested[0].type}
                  level={main_content.suggested[0].level}
                  brief={main_content.suggested[0].brief}
                  handleClickChange={this.handleClickChangeSug}
                />
      sug2= <CourseDescriptionSummary
                  id={main_content.suggested[1].id}
                  title={main_content.suggested[1].title}
                  taxon={main_content.suggested[1].text_taxon}
                  color_text={color_code[main_content.suggested[1].btaxon]}
                  icon={main_content.suggested[1].type}
                  level={main_content.suggested[1].level}
                  brief={main_content.suggested[1].brief}
                  handleClickChange={this.handleClickChangeSug}
                />
      sug3= <CourseDescriptionSummary
                  id={main_content.suggested[2].id}
                  title={main_content.suggested[2].title}
                  taxon={main_content.suggested[2].text_taxon}
                  color_text={color_code[main_content.suggested[2].btaxon]}
                  icon={main_content.suggested[2].type}
                  level={main_content.suggested[2].level}
                  brief={main_content.suggested[2].brief}
                  handleClickChange={this.handleClickChangeSug}
                />
    }

    return (

    <div>
      <div className="pageBanner">
        <Grid>
          <Row>
            <Col md={6}>
              <img src="https://www.mineducacion.gov.co/1759/articles-355157_foto_audio_principal.jpg" className="logoBanner"/>
            </Col>
            <Col md={6}>
              Login
            </Col>
          </Row>
          <Row>
            <Col md={12}>
              <h1>
                Sistema de consulta de contenidos educativos
              </h1>
              <p>
                Resultados de la Hackatón Ministerio de Educación
              </p>
              <div className="space" />
            </Col>
          </Row>
        </Grid>
      </div>
      <div className="space" />
      <Grid>
        <Row>
          <Col md={9}>
            <Row>
              <Col md={12}>
                <CourseDescription
                  id={main_content.id}
                  description={main_content.description}
                  title={main_content.title}
                  taxon={main_content.text_taxon}
                  color_text={color_code[main_content.btaxon]}
                  icon={main_content.type}
                  level={main_content.level}
                  path={main_content.path}
                />
              </Col>
            </Row>
            <Row>
              <Col md={8}>
                <h4>Cuando termines te sugerimos continuar con:</h4>
              </Col>
              <Col md={4}>
                <img src="adelante.gif" className="senasImages"/>
              </Col>
            </Row>
            <div className="space" />
            <Row>
              <Col md={4}>
                {sug1}
              </Col>
              <Col md={4}>
                {sug2}
              </Col>
              <Col md={4}>
                {sug3}
              </Col>
            </Row>
          </Col>
          <Col md={3}>
            <Row>
              <Col md={6}>
                <h4>
                  Búsqueda
                </h4>
              </Col>
              <Col md={6}>
                <img src="buscar.gif" className="senasImages"/>
              </Col>
            </Row>
            <div className="space" />
            <Row>
              <Col md={12}>
                <input 
                  className="querySpace"
                  onChange={this.handleSearchChange}
                />
                <div className="space" />
                {query_courses.map(docData => (
                  <CourseDescriptionQuery
                    key={docData.id}
                    id={docData.id}
                    title={docData.title}
                    taxon={docData.text_taxon}
                    color_text={color_code[docData.btaxon]}
                    icon={docData.type}
                    level={docData.level}
                    handleClickChange={this.handleClickChange}
                  />
                ))}
              </Col>
            </Row> 
          </Col>
        </Row>
      </Grid>
    </div>
    );
  }
}
export default App;
