import React, { PureComponent } from "react";
import { Row, Col} from 'react-bootstrap';
import "./CourseDescription.css";

class CourseDescriptionSummary extends PureComponent {

	handleClickChange = event => {
		this.props.handleClickChange(event, this.props.id);
	};

	render() {
		return (
			<div>
				<div className="courseContainerSummary">
					<Row>
						<Col md={12}>
							<Row>
								<Col md={12}>
									<h4>
										{this.props.title}
									</h4>
								</Col>
							</Row>
							<Row>
								<Col md={2}>
									<img src={this.props.icon+".png"} className="imgSmall"/>
								</Col>
								<Col md={10}>
									{this.props.brief}
								</Col>
							</Row>
							<Row>
								<div className="courseSpace" />
								<Col md={6}>
									<h4>
										{this.props.level}
									</h4>
								</Col>
								<Col md={6}>
									<h4>
										<mark style={{"background-color":this.props.color_text}}>{this.props.taxon}</mark>
									</h4>
								</Col>
							</Row>
							<Row>
								<Col md={12}>
									<div className="courseSpace" />
									<button 
										type="button"
										class="btn btn-primary"
										onClick={this.handleClickChange}
										>
										Ver
									</button>
								</Col>
							</Row>
						</Col>
					</Row>
				</div>
				<div className="courseSpace" />
			</div>
		);
	}
}

export default CourseDescriptionSummary;